.game__container {
  max-width: 825px;
  width: 100%;
  display: flex;
  flex-direction: column;
  font-family: 'Gilroy';
  font-style: normal;
  color: #FFFFFF;
  user-select: none;
}
.game__title-container {
  display: flex;
  justify-content: space-between;
  margin: 0 0 65px;
}
.game__title_close-icon,
.game-popup-exit_close-icon {
  cursor: pointer;
  height: 36px;
  width: 36px;
  transition: all 0.25s;
  background-image: url('../../assets/svg/close.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.game__title_close-icon:hover,
.game-popup-exit_close-icon:hover {
  filter: invert(42%) sepia(1%) saturate(1419%) hue-rotate(84deg) brightness(93%) contrast(90%);
  cursor: pointer;
  transform: scale(1.25);
}
.game__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 28px;
  letter-spacing: -0.5px;
}
.game__picture {
  max-width: 825px;
  height: 498px;
  margin: -21px 0 45px;
  position: relative;
}
.game__picture_image {
  height: 100%;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.game__points {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  width: 40%;
  min-width: min-content;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-self: center;
}
.game__point {
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background-color: #C4C4C4;
}
.game__point_completed {
  background-color: #FFBCA2;
}
.game__point:not(:last-child) {
  margin: 0 10px 0 0;
}
.game__answers {
  display: grid;
  grid-template-columns: minmax(min-content, 1fr) minmax(min-content, 1fr);
  grid-template-rows: min-content min-content;
  grid-gap: 10px 20px;
}
.game__answers-image {
  grid-gap: 25px;
}
.game__answer {
  display: inline-block;
  box-sizing: border-box;
  height: 62px;
  max-width: 400px;
  width: 100%;
  border-radius: 31px;
  border: 1.5px solid white;
  text-align: center;
  line-height: 32px;
  padding: 15px 30px;
  transition: all 0.25s;
  font-weight: 500;
  font-size: 22px;
  letter-spacing: -1.15px;
  color: #FFFFFF;
}
.game__answer-image {
  width: 400px;
  height: 280px;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 0.25s;
}
.game__answer:hover {
  background: white;
  color: black;
  cursor: pointer;
}
.game__answer-image:hover {
  cursor: pointer;
  transform: scale(1.03);
}
.game__answer:active {
  border-color: #FFBCA2;
  background: #FFBCA2;
  color: black;
}
.game__answer_right {
  border: 1.5px solid rgb(136, 189, 136);
  background-color: rgb(136, 189, 136);
}
.game__answer_wrong {
  border: 1.5px solid #FFBCA2;
  background-color: #FFBCA2;
}
.game__answer-image_right,
.game__answer-image_wrong {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}
.game__answer-image_right {
  background: rgba(61, 218, 105, 0.5);
}
.game__answer-image_wrong {
  background: rgba(255, 126, 126, 0.5);
}
.round-popup,
.game-popup {
  box-sizing: border-box;
  max-width: 522px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 30px;
  border-radius: 30px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.round-popup__background,
.game-popup-exit__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #010101;
  opacity: 0.7;
}
.round-popup__image {
  border-radius: 20px;
  width: 462px;
  height: 416px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}
.round-popup__image_icon {
  position: absolute;
  left: 30px;
  bottom: 30px;
  height: 60px;
  width: 60px;
}
.round-popup__title {
  color: black;
  font-weight: 500;
  font-size: 32px;
  line-height: 39px;
  text-align: center;
  margin: 40px 0 10px;
}
.round-popup__subtitle {
  color: black;
  font-style: italic;
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
  margin: 0 0 36px;
}
.round-popup__button {
  width: 400px;
  height: 62px;
  border-radius: 31px;
  border: 1.5px solid black;
  font-weight: 500;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: -1.15px;
  transition: all 0.25s;
}
.round-popup__button:hover {
  cursor: pointer;
  background-color: #FFBCA2;
  border: 1.5px solid #FFBCA2;
}
.game-popup {
  min-width: 462px;
  padding: 75px 30px 40px;
}
.game-popup__image {
  width: 100%;
  height: 178px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  margin: 0 0 10px;
}
.game-popup__title {
  color: black;
  font-weight: 700;
  font-size: 62px;
  line-height: 90px;
  text-align: center;
}
.game-popup__subtitle {
  color: black;
  font-style: normal;
  font-weight: 600;
  font-size: 42px;
  line-height: 51px;
  text-align: center;
  margin: 50px 0 30px;
}
.game-popup__buttons {
  display: grid;
  max-width: 440px;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  grid-gap: 20px;
  grid-auto-flow: column;
}
.game-popup__button {
  padding: 20px 60px;
  border: 1.5px solid #010101;
  border-radius: 30px;
  font-weight: 500;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: -1.15px;
  transition: all 0.25s;
}
.game-popup__button:hover {
  cursor: pointer;
  border: 1.5px solid #FFBCA2;
  background: #FFBCA2;
}
.game-popup-exit {
  box-sizing: border-box;
  width: 580px;
}
.game-popup-exit_close-icon {
  align-self: flex-end;
  filter: invert(42%) sepia(1%) saturate(1419%) hue-rotate(84deg) brightness(93%) contrast(90%);
}
.game-popup-exit__title {
  color: black;
  font-weight: 500;
  font-size: 32px;
  line-height: 36px;
  text-align: center;
  letter-spacing: -0.5px;
  margin: 46px 0 55px;
}
.game-popup-exit__buttons {
  max-width: none;
}
